function App() {
    const [data, setData] = useState([]);
    const [missedWords, setMissedWords] = useState([]);
    const [showComponent, setShowComponent] = useState({
        vocaContainer: false,
        formCreate: false,
        progressChart: false,
        typingApp: false,
        navMobile: false,
    });
    const handleShowVocaContainer = () => {
        setShowComponent((prevProps) => {
            return {
                ...prevProps,
                vocaContainer: !prevProps.vocaContainer,
            };
        });
    };
    const handleShowFormCreate = () => {
        setShowComponent((prevProps) => {
            return {
                ...prevProps,
                formCreate: !prevProps.formCreate,
            };
        });
    };
    const handleShowProgressChart = () => {
        setShowComponent((prevProps) => {
            return {
                ...prevProps,
                progressChart: !prevProps.progressChart,
            };
        });
    };
    const handleShowTypingApp = () => {
        setShowComponent((prevProps) => {
            return {
                ...prevProps,
                typingApp: !prevProps.typingApp,
            };
        });
    };

    useEffect(() => {
        // console.log(missedWords.length);
        const handleBeforeCloseTab = (event) => {
            if (missedWords.length > 0) {
                event.preventDefault();
                event.returnValue =
                    "Bạn có từ chưa in. Bạn có chắc muốn rời khỏi trang?";
                return "Bạn có từ chưa in. Bạn có chắc muốn rời khỏi trang?";
            }
        };
        window.addEventListener("beforeunload", handleBeforeCloseTab, {
            capture: true,
        });
        return () => {
            window.removeEventListener("beforeunload", handleBeforeCloseTab, {
                capture: true,
            });
        };
    }, [missedWords]);

    useEffect(() => {
        const addEventShowFormCreate = function (event) {
            if (event.altKey && event.key === "q") {
                // Xử lý khi người dùng bấm phím tắt "Ctrl + S"
                event.preventDefault(); // Ngăn chặn hành động mặc định của phím tắt
                handleShowFormCreate();
            }
            if (event.altKey && event.key === "Q") {
                // Xử lý khi người dùng bấm phím tắt "Ctrl + S"
                event.preventDefault(); // Ngăn chặn hành động mặc định của phím tắt
                handleShowTypingApp();
            }
        };
        document.addEventListener("keydown", addEventShowFormCreate);
        return () => {
            document.removeEventListener("keydown", addEventShowFormCreate);
        };
    }, []);

    useEffect(() => {
        const MY_HOST_NAME = "tai-thai.github.io";
        if (window.location.hostname.includes(MY_HOST_NAME)) {
            setShowComponent((prev) => ({ ...prev, navMobile: true }));
        }
    }, []);

    const update = async () => {
        console.log("here is push word func");
        const word = await API_VOCA.updateWord();
        console.log({ word: word });
    };
    const pushWord = async () => {
        console.log("here is push word func");
        const word = await API_VOCA.poshNewWord();
        console.log({ word: word });
    };
    const _delete = async () => {
        console.log("here is push word func");
        const word = await API_VOCA.deleteWord();
        console.log({ word: word });
    };
    const get = async () => {
        const word = await API_VOCA.get();
        console.log({ word: word });
    };
    // console.log({ data });
    return (
        <div className="sr-app">
            {/* <header className="sr-app-header">
                <button onClick={get}>get</button>
                <button onClick={pushWord}>pushWord</button>
                <button onClick={update}>update</button>
                <button onClick={_delete}>delete</button>
            </header> */}
            {/* hello achieve achievements this those */}
            <main>
                {showComponent.vocaContainer && (
                    <VocaContainer
                        handleShowComponent={handleShowVocaContainer}
                        data={data}
                        setData={setData}
                        missedWords={missedWords}
                        setMissedWords={setMissedWords}
                    />
                )}
                {showComponent.formCreate && (
                    <FormCreate
                        setShowFormCreate={handleShowFormCreate}
                        setShowVocaContainer={handleShowVocaContainer}
                        setShowProgressChart={handleShowProgressChart}
                        setShowTypingApp={handleShowTypingApp}
                        data={data}
                        setData={setData}
                    />
                )}
                {showComponent.progressChart && (
                    <ProgressChart
                        setShowComponent={handleShowProgressChart}
                        data={data}
                        setData={setData}
                    />
                )}
                {showComponent.typingApp && (
                    <TypingApp
                        setShowComponent={handleShowTypingApp}
                        data={data}
                        setData={setData}
                    />
                )}
            </main>
            {showComponent.navMobile && (
                <NavMobile
                    handleShowVocaContainer={handleShowVocaContainer}
                    handleShowFormCreate={handleShowFormCreate}
                    handleShowProgressChart={handleShowProgressChart}
                    handleShowTypingApp={handleShowTypingApp}
                />
            )}
        </div>
    );
}

export default App;
