import React, { useEffect, useState } from "react";
import API_VOCA from "../firebase/API_VOCA";
import VocaContainer from "./VocaContainer";
import SearchField from "./SearchField";

const ProgressChart = ({ setShowComponent, data, setData }) => {
    const [progressData, setProgressData] = useState([]);
    const [maxWordRepetition, setmaxWordRepetition] = useState(0);
    const [totalVocab, setTotalVocab] = useState(0);
    const [totalVocaOvertime, setTotalVocaOvertime] = useState(0);
    const [totalVocaGonnaOvertime, setTotalVocaGonnaOvertime] = useState(0);
    const [showVocaContainer, setShowVocaContainer] = useState(false);
    const [activeLevel, setActiveLevel] = useState(0);
    // console.log(data);
    useEffect(() => {
        const handleProgressData = async () => {
            // const dataStorage = await API_VOCA.get(false);
            let dataStorage = data;
            if (data.length <= 0) {
                console.log("fetch data progress container");
                dataStorage = await API_VOCA.get();
                setData(dataStorage);
            }
            let maxWordRepetition = 0;
            let _totalVocaOvertime = 0;
            let _totalVocaGonnaOvertime = 0;
            const _progressResult = dataStorage.reduce((arr, el) => {
                const level = el.level; // - 1 current time When create word
                if (arr[level]) {
                    arr[level].push(el);
                    if (maxWordRepetition < arr[level].length) {
                        maxWordRepetition = arr[level].length;
                    }
                } else {
                    arr[level] = [];
                    arr[level].push(el);
                }
                // overtime word
                const currentTime = new Date().getTime();
                const remainingTime = el.repetitiveTime - currentTime;
                if (remainingTime <= 0) {
                    _totalVocaOvertime++;
                } else if (remainingTime > 0 && remainingTime < 43200000) {
                    _totalVocaGonnaOvertime++;
                }
                return arr;
            }, []);
            const progressResult = replaceEmpty(_progressResult);

            setProgressData(progressResult);
            setmaxWordRepetition(maxWordRepetition);
            setTotalVocab(dataStorage.length);
            setTotalVocaOvertime(_totalVocaOvertime);
            setTotalVocaGonnaOvertime(_totalVocaGonnaOvertime);
            // console.log({ progressResult });

            function replaceEmpty(arr) {
                for (let i = 0; i < arr.length; i++) {
                    if (!arr[i]) {
                        arr[i] = [];
                    }
                }
                return arr;
            }
        };

        handleProgressData();
        return () => {};
    }, []);
    // useEffect(() => {
    //     const handleProgressData = async () => {
    //         // const dataStorage = await API_VOCA.get(false);
    //         let dataStorage = data;
    //         if (data.length <= 0) {
    //             console.log("fetch data progress container");
    //             dataStorage = await API_VOCA.get(false);
    //             setData(dataStorage);
    //         }
    //         let maxWordRepetition = 0;
    //         const _progressResult = dataStorage.reduce((arr, el) => {
    //             const level = el.level; // - 1 current time When create word
    //             if (arr[level]) {
    //                 ++arr[level];
    //                 if (maxWordRepetition < arr[level]) {
    //                     maxWordRepetition = arr[level];
    //                 }
    //             } else {
    //                 arr[level] = 1;
    //             }
    //             return arr;
    //         }, []);
    //         const progressResult = replaceEmpty(_progressResult);

    //         setProgressData(progressResult);
    //         setmaxWordRepetition(maxWordRepetition);
    //         setTotalVocab(dataStorage.length);
    //         // console.log({ progressResult });

    //         function replaceEmpty(arr) {
    //             for (let i = 0; i < arr.length; i++) {
    //                 if (!arr[i]) {
    //                     arr[i] = 0;
    //                 }
    //             }
    //             return arr;
    //         }
    //     };

    //     handleProgressData();
    //     return () => {};
    // }, []);

    const handleOnClickBar = (level) => {
        handleShowVocaContainer();
        setActiveLevel(level);
    };

    const handleShowVocaContainer = () => {
        setShowVocaContainer((prev) => !prev);
    };

    const handleSetData = (level) => {
        return (callbackSetDataLevel) => {
            const newDataLevel = callbackSetDataLevel(progressData[level]);
            const newData = [...progressData];
            newData[level] = newDataLevel;
            setProgressData(newData);
        };
    };

    const getDataLevel = (level) => progressData[level];

    return (
        <div>
            {showVocaContainer && (
                <VocaContainer
                    handleShowComponent={handleShowVocaContainer}
                    data={getDataLevel(activeLevel)}
                    setData={handleSetData(activeLevel)}
                />
            )}
            <div className="sr-progress-chart sr-container">
                <button
                    className="close-btn progress-chart-close-btn"
                    onClick={setShowComponent}
                >
                    <span className="close-icon">×</span>
                </button>
                <button
                    style={{
                        position: "fixed",
                        top: "10px",
                        left: "10px",
                        padding: "10px",
                    }}
                    className="sr-btn sr-bg-secondary"
                >
                    Total {totalVocaOvertime}/{totalVocaGonnaOvertime}/
                    {totalVocab}
                </button>
                {progressData &&
                    progressData.length > 0 &&
                    progressData.map((dataLevel, index) => {
                        return (
                            <div
                                class={`bar  ${
                                    dataLevel.length == 0 ? `bar-empty` : ""
                                }`}
                                data-counter={dataLevel.length}
                                data-level={index}
                                style={{
                                    height: dataLevel.length
                                        ? (dataLevel.length /
                                              maxWordRepetition) *
                                              100 +
                                          "%"
                                        : "10px",
                                }}
                                onClick={() => handleOnClickBar(index)}
                            ></div>
                        );
                    })}
            </div>
        </div>
    );
};

export default ProgressChart;
