import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom/client";
import CardItem from "./CardItem";
import PrintWindow from "./PrintWindow";
import printWindowStyle from "../utils/printWindowStyle.js";

function MissedContainer({ data = [], setData }) {
    const [showComponent, setShowComponent] = useState(false);

    const handlePrint = (e) => {
        e.stopPropagation();
        if (data.length <= 0) return;
        // create print window
        var printWindow = window.open(
            "",
            "Print Window",
            "height='100vh',width='100vw'"
        );
        printWindow.document.write(
            "<html><head><title>Print Window</title>" +
                printWindowStyle +
                "</head><body ></body></html>"
        );

        // const templatePrintWindow = PrintWindow(data);
        // printWindow.document.write(templatePrintWindow);
        // printWindow.document.write("</body></html>");

        // component print window
        const PRINT_ROOT = printWindow.document.createElement("div");
        printWindow.document.body.appendChild(PRINT_ROOT);
        const root = ReactDOM.createRoot(PRINT_ROOT);
        root.render(<PrintWindow data={data} />);

        printWindow.document.close();
        printWindow.focus();
        printWindow.print();
        // printWindow.close();
        // printWindow.onafterprint = function () {
        //     printWindow.close();
        // };
    };

    const handleClear = () => {
        // set data = []
        setData([]);
    };
    return (
        <div className="sr-search-container">
            <h2
                style={{ width: "fit-content" }}
                className="sr-cl-secondary"
                onClick={() => setShowComponent((prev) => !prev)}
            >
                Missed ({data.length})
                <button
                    style={{ padding: "10px", marginLeft: "15px" }}
                    className="sr-btn sr-bg-primary"
                    onClick={handlePrint}
                >
                    Print
                </button>
                <button
                    style={{ padding: "10px", marginLeft: "15px" }}
                    className="sr-btn sr-bg-secondary"
                    onClick={handleClear}
                >
                    Clear
                </button>
            </h2>
            {showComponent && (
                <div className="sr-search-results">
                    {data.length > 0 &&
                        data.map((element, index) => (
                            <CardItem
                                key={element.id}
                                {...element}
                                indexInData={index}
                                setData={setData}
                            />
                        ))}
                </div>
            )}
        </div>
    );
}

export default MissedContainer;
