import React, { useEffect, useState } from "react";
import CardItem from "./CardItem";

function SearchField({ searchValue: searchValueOutSide = "", data, setData }) {
    const [searchData, setSearchData] = useState([]);
    const [searchValueInside, setSearchValueInside] = useState("");
    const [showInput, setShowInput] = useState(false);

    useEffect(() => {
        const timerId = setTimeout(() => {
            // console.log({ searchValueOutSide, searchValueInside });
            const searchValue =
                searchValueInside.trim().toUpperCase() ||
                searchValueOutSide.trim().toUpperCase();
            // vocab.voca.toUpperCase().includes(searchValue) || vocab.note.toUpperCase().includes(searchValue) || vocab.examples.toUpperCase().includes(searchValue)
            // console.log(searchValue);
            if (searchValue && searchValue.length > 1 && data.length > 0) {
                const _searchData = data
                    .map((vocab, index) => {
                        if (
                            vocab.voca.toUpperCase().includes(searchValue) ||
                            vocab.note.toUpperCase().includes(searchValue) ||
                            vocab.translate.toUpperCase().includes(searchValue)
                        ) {
                            return { ...vocab, indexInData: index };
                        }
                        return null;
                    })
                    .filter(Boolean);
                // console.log({ _searchData });
                setSearchData(_searchData);
            } else if (!searchValue) {
                setSearchData([]);
            }
        }, 400);
        return () => {
            clearTimeout(timerId);
        };
    }, [searchValueOutSide, searchValueInside]);

    const handleGetPlainText = async (e) => {
        const _text = await navigator.clipboard.readText();
        const text = _text.trim();
        e.target.value = text;
        await navigator.clipboard.writeText(text);
    };

    return (
        <div className="sr-search-container">
            <h2
                style={{ width: "fit-content" }}
                className="sr-cl-secondary"
                onClick={() => setShowInput((prev) => !prev)}
            >
                Search ({searchData.length})
            </h2>
            {showInput && (
                <>
                    <div
                        style={{
                            display: "flex",
                            alignItems: "center",
                            justifyContent: "center",
                        }}
                    >
                        <input
                            className="sr-search-input"
                            type="text"
                            value={searchValueInside}
                            onChange={(e) =>
                                setSearchValueInside(e.target.value)
                            }
                        />
                        <input
                            style={{ width: "50px" }}
                            className="sr-search-input"
                            type="text"
                            // value={}
                            placeholder="copy"
                            onBlur={(e) => (e.target.value = "")}
                            onFocus={handleGetPlainText}
                        />
                    </div>
                    <div className="sr-search-results">
                        {searchData.length > 0 &&
                            searchData
                                .slice(0, 10)
                                ?.map((element, index) => (
                                    <CardItem
                                        key={element.id}
                                        {...element}
                                        indexInData={index}
                                        setData={setSearchData}
                                        setUpperDataSearch={setData}
                                        indexInUpperData={element.indexInData}
                                        searchText={searchValueInside.trim()}
                                    />
                                ))}
                    </div>
                </>
            )}
        </div>
    );
}

export default SearchField;
