import React, { useState } from "react";
import PropTypes from "prop-types";
import convertMilliseconds from "../utils/convertMilliseconds";
import schedule from "../utils/schedule";
import API_VOCA from "../firebase/API_VOCA";
import textToHighlight from "../utils/textToHighlight";
import { useEffect } from "react";
import { useRef } from "react";

const CardItem = (props) => {
    const {
        voca,
        translate,
        meaning,
        imageUrl,
        examples,
        note,
        spacedRepetition,
        time,
        spelling,
        typeWords,
        repetitiveTime,
        level,
        id,
        indexInData,
        setData,
        setMissedWords,
        setUpperDataSearch,
        indexInUpperData,
        searchText,
    } = props;

    const [showDetail, setShowDetail] = useState(false);
    const [wordInfo, setWordInfo] = useState({
        note: note,
        examples: examples,
    });
    const currentTime = new Date().getTime();
    const remainingTime = repetitiveTime - currentTime;

    // too much vocabulary remain
    // useEffect(() => {
    //     // 12h = 43200000
    //     console.log(remainingTime);
    //     if (remainingTime < 43200000) {
    //         const time = Math.random() * 5;
    //         console.log(time);
    //         setTimeout(() => {
    //             handlePrevLevel();
    //             console.log("prev level");
    //         }, time);
    //     }
    // }, []);

    const handleNextLevel = async () => {
        const currentTime = new Date().getTime();
        let _time = [];
        let nextLevel = level + 1;

        // console.log({ level, voca, indexInData });
        if (level + 1 >= schedule.normal.length) {
            console.log("===== RETURN LEVEL 0 =====");
            _time = [currentTime, currentTime + schedule.normal[0]];
            nextLevel = 0;
        } else {
            _time = [...time, currentTime + schedule.normal[level + 1]];
        }

        const updateData = {
            time: _time,
            level: nextLevel,
            repetitiveTime: _time[_time.length - 1], // end of time = _time[_time.length - 1]
        };

        // Not important
        const _remainingTime = updateData.repetitiveTime - currentTime;
        console.log(
            convertMilliseconds(_remainingTime).toString({
                seconds: true,
            })
        );

        // Update API
        await API_VOCA.updateWord(id, updateData);
        // Update Ui
        setData((prevData) => {
            const newData = [
                ...prevData,
                {
                    ...prevData[indexInData],
                    ...updateData,
                    note: wordInfo.note,
                    examples: wordInfo.examples,
                },
            ];
            newData.splice(indexInData, 1);
            return newData;
        });
    };

    const handlePrevLevel = async () => {
        const currentTime = new Date().getTime();
        let _time = time;
        const timeLength = time.length;

        if (timeLength >= 2) {
            if (timeLength == 2) {
                _time.pop();
            } else {
                _time.pop();
                _time.pop();
            }
            // console.log(_time);
            _time = [..._time, currentTime + schedule.normal[timeLength - 2]];

            const updateData = {
                time: _time,
                level: timeLength - 2,
                repetitiveTime: _time[_time.length - 1], // end of time = _time[_time.length - 1]
            };

            // Not important
            const _remainingTime = updateData.repetitiveTime - currentTime;
            console.log(
                convertMilliseconds(_remainingTime).toString({
                    seconds: true,
                })
            );

            // Update API
            await API_VOCA.updateWord(id, updateData);

            // Update Ui
            setData((prevData) => {
                const newData = [
                    ...prevData,
                    {
                        ...prevData[indexInData],
                        ...updateData,
                        note: wordInfo.note,
                        examples: wordInfo.examples,
                    },
                ];

                if (setMissedWords) {
                    setMissedWords((missedWordsPrevData) => {
                        const newData = [
                            ...missedWordsPrevData,
                            {
                                ...prevData[indexInData],
                                ...updateData,
                                note: wordInfo.note,
                                examples: wordInfo.examples,
                            },
                        ];
                        return newData;
                    });
                }
                newData.splice(indexInData, 1);
                return newData;
            });
            // setMissedWords((prevData) => {
            //     const newData = [
            //         ...prevData,
            //         {
            //             voca,
            //             translate,
            //             meaning,
            //             imageUrl,
            //             examples,
            //             note,
            //             spacedRepetition,
            //             time,
            //             spelling,
            //             typeWords,
            //             repetitiveTime,
            //             level,
            //             id,
            //             ...updateData,
            //         },
            //     ];
            //     return newData;
            // });
        }
    };

    const handleDeleteWord = async () => {
        // Update API
        await API_VOCA.deleteWord(id);

        // Update Ui
        setData((prevData) => {
            const newData = [...prevData];
            newData.splice(indexInData, 1);
            return newData;
        });
    };

    const handleOnBlur = async (e) => {
        // console.log(e.target.innerHTML);
        const target = e.target;
        if (props[target.id].trim() !== target.innerText.trim()) {
            const updateData = {
                [target.id]: target.innerText,
            };
            await API_VOCA.updateWord(id, updateData);

            // handle UI
            // console.log({ setUpperDataSearch });
            if (setUpperDataSearch) {
                setUpperDataSearch((prevData) => {
                    // console.log(prevData[indexInUpperData]);
                    prevData[indexInUpperData][target.id] = target.innerText;
                    // console.log(prevData[indexInUpperData]);
                    return prevData;
                });
            }
        }
        // console.log({ updateData });
    };

    const handleKeydownHighlight = (e) => {
        if (e.keyCode == "50") {
            handleOnBlurHighlight(e, false);
            // var range = document.createRange(); // Tạo một đối tượng Range
            // range.selectNodeContents(e.target); // Chọn toàn bộ nội dung của thẻ div
            // range.collapse(false); // Di chuyển con trỏ chuột đến cuối của văn bản

            // var selection = window.getSelection(); // Lấy đối tượng Selection
            // selection.removeAllRanges(); // Xóa bỏ tất cả các range hiện tại
            // selection.addRange(range); // Thêm range mới đã được thiết lập
            // console.log({ range, selection });
        }
    };
    const handleOnBlurHighlight = async (e, updateApi = true) => {
        // console.log(e.target.innerHTML);
        const target = e.target;
        const plainText = target.innerHTML;
        if (wordInfo[target.id].trim() !== plainText.trim()) {
            const highlightText = await textToHighlight(plainText);
            const updateData = {
                [target.id]: highlightText,
            };

            if (updateApi) {
                await API_VOCA.updateWord(id, updateData);
            }
            target.innerHTML = highlightText;
            setWordInfo((prev) => ({ ...prev, [target.id]: highlightText }));
            // console.log("on Blur");

            // handle UI
            // console.log({ setUpperDataSearch, indexInUpperData });
            if (setUpperDataSearch && indexInUpperData >= 0) {
                setUpperDataSearch((prevData) => {
                    prevData[indexInUpperData][target.id] = highlightText;
                    return prevData;
                });
            }
        }
        // console.log({ updateData });
    };

    const copyToClipboard = () => {
        navigator.clipboard.writeText(voca);
    };

    // console.log({ remainingTime }, convertMilliseconds(remainingTime));
    return (
        <div
            className={`sr-card sr-container ${
                showDetail && "sr-show-detail-voca"
            }`}
            // style="order: -57142365"
            // style={{ order: remainingTime }}
            data-level={level}
        >
            <div className="sr-card-header">
                <h2
                    id="voca"
                    className="sr-title"
                    contentEditable="true"
                    onBlur={handleOnBlur}
                    dangerouslySetInnerHTML={{
                        __html: voca.onHighlightText(searchText),
                    }}
                >
                    {/* {} */}
                </h2>
                <button onClick={copyToClipboard} className="">
                    cp
                </button>
                {typeWords && typeWords.length > 0 && (
                    <div className="sr-type-word-container">
                        {typeWords.map((typeWord) => (
                            <span className="type-words">
                                (<span id="typeWords">{typeWord}</span>)
                            </span>
                        ))}
                    </div>
                )}

                <span className="spelling">
                    /
                    <span
                        id="spelling"
                        contentEditable="true"
                        onBlur={handleOnBlur}
                        dangerouslySetInnerHTML={{
                            __html:
                                spelling.onHighlightText(searchText) ||
                                "spelling",
                        }}
                    ></span>
                    /
                </span>
            </div>

            <div>
                {/* {!meaning && translate && (
                    <p
                        id="translate"
                        className="note translate"
                        contentEditable="true"
                        onBlur={handleOnBlur}
                    >
                        {translate}
                    </p>
                )} */}

                <div>
                    <span
                        id="meaning"
                        className="note meaning"
                        contentEditable="true"
                        onBlur={handleOnBlur}
                        dangerouslySetInnerHTML={{
                            __html: meaning.onHighlightText(searchText),
                        }}
                    ></span>
                    <>
                        <span className="sr-meaning-flag">|</span>
                        <p
                            id="translate"
                            className="note sr-translate"
                            contentEditable="true"
                            onBlur={handleOnBlur}
                            dangerouslySetInnerHTML={{
                                __html:
                                    translate.onHighlightText(searchText) ||
                                    "translate",
                            }}
                        ></p>
                    </>
                </div>
            </div>

            {remainingTime > 0 ? (
                <p
                    class="note remaining-time"
                    onClick={() => setShowDetail(!showDetail)}
                >
                    {convertMilliseconds(remainingTime).toString({
                        seconds: true,
                    })}
                </p>
            ) : (
                <p
                    class="note over-time"
                    onClick={() => setShowDetail(!showDetail)}
                >
                    over time
                </p>
            )}

            <div
                id="note"
                className="sr-note"
                contentEditable="true"
                onBlur={handleOnBlurHighlight}
                onKeyDown={handleKeydownHighlight}
                dangerouslySetInnerHTML={{
                    __html: note,
                }}
            ></div>
            <div
                id="examples"
                className="example-container sr-example"
                contentEditable="true"
                onBlur={handleOnBlurHighlight}
                onKeyDown={handleKeydownHighlight}
                dangerouslySetInnerHTML={{
                    __html: examples,
                }}
            >
                {/* {examples} */}
            </div>

            <div className="card-actions">
                <button
                    className="sr-btn sr-bg-danger btn-outline"
                    onClick={handleDeleteWord}
                >
                    Delete
                </button>
                <button
                    className="sr-btn sr-bg-secondary btn-outline"
                    onClick={handlePrevLevel}
                >
                    Prev level
                </button>
                <button
                    className="sr-btn sr-bg-primary"
                    onClick={handleNextLevel}
                >
                    Next level
                </button>
            </div>
        </div>
    );
};

CardItem.propTypes = {};

export default CardItem;
